.banner {
  height: 210px;
  background: #152d82 url("/@@/images/news/newsbg.jpg") no-repeat 50% -70px;
  padding: 0 1.2rem;
  h2 {
    padding: 0 0 0 10%;
    margin: 0;
    line-height: 210px;
    color: #f5f5f5;
    font-size: .6rem;
    font-weight: 100;
  }
}
@media(max-width:750px) {
  .banner {
    height: 180px;
    h2 {
      line-height: 180px;
    }
  }
}