header {
  position: fixed;
  width: 100%;
  z-index: 99;
  padding: .4rem 1rem;
  background: rgba(0, 0, 0, 0.1);
  nav {
    float: right;
    a {
      font-size: .28rem;
      font-weight: 100;
      margin: 0 0.22rem 0 0;
      position: relative;
      display: inline-block;
      &:after {
        content: " ";
        transition: all .5s;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 2px;
        background-color: transparent;
      }
      &.active:after,
      &:hover:after {
        transition: all .5s;
        background-color: #3ff8ff;
        bottom: -.15rem;
      }
    }
    #news {
      padding: 0.1rem .2rem;
      border-radius: .2rem;
      background-color: #0fa5e4;
      background-image: -moz-linear-gradient( 128deg, rgb(15, 165, 228) 0%, rgb(40, 140, 197) 100%);
      background-image: -webkit-linear-gradient( 128deg, rgb(15, 165, 228) 0%, rgb(40, 140, 197) 100%);
      background-image: -ms-linear-gradient( 128deg, rgb(15, 165, 228) 0%, rgb(40, 140, 197) 100%);
      transition: all .5s;
      &:after {
        display: none;
      }
      &:hover {
        background-image: -moz-linear-gradient( 128deg, #14a1dc 0%, #1d628a 100%);
        background-image: -webkit-linear-gradient( 128deg, #14a1dc 0%, #1d628a 100%);
        background-image: -ms-linear-gradient( 128deg, #14a1dc 0%, #1d628a 100%);
      }
    }
  }
  #personinfo {
    display: none;
    position: absolute;
    width: 140px;    
    right: 0;
    top: 0;
    height: 100%;
    #avatar {
      width: .8rem;
      height: .8rem;
      border-radius: 50%;
      margin-top: 0.3rem;
      margin-left: 0.2rem;
    }
    #username {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      font-size: .28rem;
      margin-left: .2rem;
    }
    ul{
      position: absolute;
      width: 100%;
      padding-left: 0.2rem;
      background: rgba(0, 0, 0, 0.1);
      top: 100%;
      left: 0;
      overflow: hidden;
      height: 0;
      transition: all .5s;
      li{
        font-size: .3rem;
        line-height: 1.2rem;
      }
    }
    &:hover{
      ul{
        height: 1.2rem
      }
    }
  }
  &.haslogined {
    padding-right: 140px;
    #login,
    #reg {
      display: none;
    }
    #personinfo {
      display: block;
    }
  }
}

@media(max-width:768px) {
  header {
    padding: .2rem .4rem;
    img {
      width: 1.8rem;
    }
    nav {
      a {
        margin: 0.07rem 0.2rem 0 0;
        &:nth-of-type(-n+5) {
          display: none;
        }
      }
      #news {
        padding: 0.05rem .1rem;
      }
    }
    #personinfo {
      #avatar {
        width: .8rem;
        height: .8rem;
        border-radius: 50%;
        margin-top: 0.1rem;
        margin-left: 0.2rem;
      }
    }    
  }
}

@media(max-width:480px) {
  header {
    &.haslogined{
      padding-right: 90px;
    }
    #personinfo{
      width: 90px;
    }
    img {
      margin-top: 0.05rem
    }
  }
}