@import "./common/normalize.scss"; 
@import "./common/global.scss";
@import "./common/tools.scss";
@import "./common/animate.scss";
@import "./includes/header.scss";
@import "./includes/banner.scss";
@import "./includes/footer.scss";
.container { 
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
}
 
header {
  background-color: #122466;
  position: relative;
} 
   
#news-list {
  @include cf;
  li {
    float: left;
    width: 32%;
    margin: .8rem 2% 0 0;
    padding: 0.35rem;
    border: 1px solid transparent;
    transition: all .5s;
    img {
      width: 100%;
    }
    h3 {
      font-size: .35rem;
      padding: .3rem 0;
      color: #222;
      font-weight: 400;
    }
    p {
      color: #0a1026;
      font-size: .25rem;
      line-height: .4rem;
    }
    span {
      margin-top: .1rem;
      font-size: 12px;
      color: #999
    }
    &:hover {
      border-color: #ddd;
    }
    &:nth-of-type(3n) {
      margin-right: 0
    }
  }
}

#laypage {
  padding: 0.4rem;
  text-align: right;
  color: #999;
  a {
    color: #999;
    font-size: 0.3rem;
    padding: 0 0.1rem;
    transition: color .5s;
    &.on,
    &:hover {
      color: #2caaeb
    }
  }
}

main.container {
  margin: .7rem auto;
  h1 {
    font-size: .6rem;
    color: #222;
    margin: .5rem 0;
  }
  .brandnav {
    color: #308de3;
    font-size: 18px;
    font-weight: 400;
    a {
      color: #308de3
    }
  }
  aside {
    color: #606266;
    font-size: 16px;
    border-bottom: 1px solid #ddd;
    height: 30px;
    line-height: 30px;
    .author {
      float: right
    }
  }
  .content {
    font-size: 16px;
    color: #606266;
    line-height: 1.8;
    img{
      margin: .5rem 0;
    }
  }
}

@media(max-width:768px) {
  #news-list {
    padding: 0 5px;
    li {
      width: 47.5%;
      margin: .5rem 5% 0 0;
      &:nth-of-type(3n) {
        margin-right: 5%
      }
      &:nth-of-type(2n) {
        margin-right: 0
      }
    }
  }
  main.container{
    padding: 0 20px
  }
}